import { SalesforceConnect } from 'models';

export class Application {

	constructor(params, authCallback){

		this.lightnings = {};
		this.messages = [];

		this.debugTick = 0;
		this.lightningsHanlder = {}
		this.handlerAuth = null;
		this.authenticated = false;
		this.authCallback = authCallback;
		this.params = params;

		this.resolveRefresh = null;
		this.rejectRefresh = null;

		this.bindEvent(window, 'message', this.onPostMessage);
		this.onInitAuth();
	}

	onInitAuth = () =>{
		setTimeout(()=>{
			console.log('init auth');

			const node = document.createElement("DIV");
			node.id = 'salesforce-auth';
			node.style.cssText = 'display: none;width: 0px;height: 0px;';
			
			document.body.appendChild(node);

			let fallback = localStorage.getItem('fallback');
			if(fallback === 'true') this.authenticated = true;

			this.handlerAuth = new SalesforceConnect('mobilityAuth','salesforce-auth', {auth:true, visualforce: 'javascriptIntegrationInit', ...this.params});

		}, 10)
	}

	refresh(){
		if(this.resolveRefresh) this.rejectRefreshProxy(new Error('New request refresh. No longer valid promise'));

		return new Promise((resolve, reject)=>{
			this.resolveRefresh = resolve;
			this.rejectRefresh = reject;

			this.handlerAuth.refresh()
		});
	}

	resolveRefreshProxy = (data) => {
		this.resolveRefresh(data);
		this.resolveRefresh = null;
		this.rejectRefresh = null;
	}

	rejectRefreshProxy = (err) => {
		this.rejectRefresh(err);
		this.resolveRefresh = null;
		this.rejectRefresh = null;
	}

	onPostMessage = (e) => {
		if(e.data.action){		
			console.log('onPostMessage', e.data)

			if(e.data.action === "HOOK_REQUEST"){
				this.onHooked(e.data);
			}else if(e.data.action === "AUTH_CONFIRM"){
				this.onAuth(e.data);
			}else if(e.data.action === "AUTH_REFRESH"){
				this.resolveRefresh(e.data.params);
			}else{
				this.onReciveMessage(e.data);
			}
		}
	}

	bindEvent(element, eventName, eventHandler) {
	    if (element.addEventListener) {
	        element.addEventListener(eventName, eventHandler, false);
	    } else if (element.attachEvent) {
	        element.attachEvent('on' + eventName, eventHandler);
	    }
	}

	handlerComponent = e => {
		if(e !== null) this.lightningsHanlder[e.id] = e
	}

	onAuth = (data) => {
		console.log('Authenticated');

		this.authenticated = true;

		for(let index in this.lightningsHanlder){
			const lght = this.lightningsHanlder[index];

			if(lght && lght.onAuth) lght.onAuth();
		}

		if(this.authCallback) this.authCallback(data.params);
	}

	onHooked = (data) => {
		let target = this.lightningsHanlder[data.id];
		let name = data.id;
		let value = {target: target, data: data};

		this.lightnings[name] = value;

		target.onHook();
	}

	onReciveMessage(e){
		let id = e.id;
		let lightning = this.lightnings[id];

		if(lightning && lightning.target){
			lightning.target.dispatcher(e);

			this.messages.push(e);
		}
	}

}