import React from 'react';
import ReactDOM from 'react-dom';

import { Lightning } from 'components';

export class SalesforceConnect{

	component;
	htmlId;
	domElement = null;
	handler = null;
	options = {};

	constructor(component, htmlId, options = {}){
		this.component = component;
		this.htmlId = htmlId;
		this.options = options;

		this.domElement = document.getElementById(htmlId);

		if(this.domElement){
			ReactDOM.render(this.render(), this.domElement);
		}
	}

	handlerComponent = (e) => {
		console.log('handlerComponent', this.htmlId);
		this.handler = e;

		window.Application.handlerComponent(e);
	}

	refresh = () => {
		this.handler.refresh();
	}

	render = () => {
		return (
			<Lightning 
				ref={this.handlerComponent}
				component={this.component}
				params={this.options}
			/>
		)
	}

	renerError = (msg) => (
		<div>
			{msg}
		</div>
	)
}