import * as moment from 'moment';

export class SalesforceAuth {

	static communityURI 	= null;
	static clientId 		= null;

	static session 			= null;

	static setup(communityURI = null, clientId = null){
		if(communityURI) 	SalesforceAuth.communityURI = communityURI;
		if(clientId) 		SalesforceAuth.clientId = clientId;
	}

	static login(redirectURI) {
		let url = `${SalesforceAuth.communityURI}services/oauth2/authorize?display=popup&response_type=token&client_id=${encodeURIComponent(SalesforceAuth.clientId)}&redirect_uri=${encodeURIComponent(redirectURI)}`;
		SalesforceAuth.openUrl(url, 'Salessforce login', 700, 600);
	}

	static get salesforceURI(){
		return SalesforceAuth.session.instance_url.replace(/(https:\/\/[\w-]+\.)([\w]+)\.(my\.salesforce\.com)/gm, `$1lightning.force.com`);
	}

	static handleAuthentication(){
		if (!window.location.hash)  return null;

		let session = {};
		let message = decodeURIComponent(window.location.hash.substr(1));
		let params = message.split('&');

		params.forEach(function (param) {
			let splitter = param.split('=');
			session[splitter[0]] = splitter[1];
		});
		
		return session
	}

	static logout() {
		SalesforceAuth.session = null;
	}

	static setSession(session){
		SalesforceAuth.session = session;
	}

	static isAuthenticated() {
		let session = SalesforceAuth.session;

		if(!session) return false;
		if(!session && !session.access_token) return false;
		
		let timestamp = Number(moment().format('x')) / 1000;
		let issuedAt = Number(session.issued_at) / 1000;

		if(session && timestamp - issuedAt > 60*60*24) return false;

		return (session && !!session.access_token)
	}

	static openUrl(url, title, w, h) {
		// Handles dual monitor setups
		//var parentLeft = window.screenLeft ? window.screenLeft : window.screenX;
		//var parentTop = window.screenTop ? window.screenTop : window.screenY;
		//var left = parentLeft + (window.innerWidth / 2) - (w / 2);
		//var top = parentTop + (window.innerHeight / 2) - (h / 2);

		return window.location = url;//, title, 'width=' + w + ', height=' + h + ', top=' + top + ', left=' + left);
	}
}
