import React from 'react';
import { Lib } from 'vendor/utils';

export class Abstract extends React.Component{

	get urlParams(){
		return Lib.getUrlParams()
	}

	onChange = (e) => {
		let name = e.target.name;
		let value = e.target.value;

		this.setState({
			[name]: value
		})
	}

}