import React from 'react';

import $ from 'jquery';

import { SalesforceAuth } from 'models';
import { Lib } from 'vendor/utils';
import { Render } from 'vendor/react/components';

export class Lightning extends React.Component{

	constructor(props){
		super(props);

		let authenticated = window.Application.authenticated;

		if(props.params.auth){
			authenticated = true;
		}

		this.state = {
			redirect: null,
			authenticated
		}

		this.iframeHandler = null;
		this.callbackLoad = null;

		this.id = props.component;//Lib.genGuid();
	}

	get handler(){
		return $(this.iframeHandler)
	}

	get height(){
		return this.handler.height();
	}

	get width(){
		return this.handler.width();
	}

	onAuth = () =>{
		this.setState({
			authenticated: true
		});
	}

	onHook = () => {
		this.resize();
		if(this.props.params.callbackLoad) this.props.params.callbackLoad(this);
	}

	inizializeLightningOut(){
		let params = {
			id: this.id,
			lwc: this.props.params.lwc
		};

		params = {...params, ...this.props.params};

		let componentProps = {params, postMessage: ()=>{}};

		console.log("params", params);
		console.log("componentProps", componentProps);

		let session = SalesforceAuth.session;

		if(!session) return;
		if(!session.access_token) return;

		window.$Lightning.use("c:javascriptPage", () => {
			console.log('Init');

			window.$Lightning.createComponent(`c:${this.props.component}`, componentProps, this.id, (cmp) => {
				console.log(cmp);
			})
		}, SalesforceAuth.salesforceURI, session.access_token);
	}

	queryString(propsParams){
		let params = [];

		let exludeKeys = [
			'callback',
			'lwc',
			'iframeClass',
			'customUrl',
			'breadcrumb'
		];

		for(let paramId in propsParams){
			let value = propsParams[paramId];

			if(!exludeKeys.includes(paramId)){
				params.push(`${paramId}=${value}`);
			}
		}

		if(propsParams.breadcrumb){
			const breadcrumbLabels = [];

			for(let breadcrumb of propsParams.breadcrumb){
				breadcrumbLabels.push(breadcrumb.label);
			}

			params.push(`breadcrumbLabels=${breadcrumbLabels.join(',')}`);
		}

		return params.join("&");
	}

	onUpdate = () => {	
		if(this.props.onUpdate) this.props.onUpdate(this);

		this.resize();
	}

	refresh(){
		this.sendMessage({action:'REFRESH'});
	}

	resize(){
		let width = $(window).width();
		let type = '';

		if(width > 1200){
			type = 'large-desktop';
		}else if(width > 992){
			type = 'desktop';
		}else if(width > 768){
			type = 'tablet';	
		}else{
			type = 'phone';	
		}

		this.sendMessage({action:'RESIZE', data: {
			height: $(window).height(),
			width,
			type
		}})
	}

	dispatcher = e => {
		if(this.props.params.lwc) return;

		switch(e.action){
			case "RESIZE":
				$(`#${this.id}`).height(e.height);
				break;
			case "GOTO":
				window.location = e.url;
				break;
			case "OPEN_CALLBACK":
				if(this.props.params.callback) this.props.params.callback(e.params);
				break;
			case "NAVIGATETO":
				debugger;
				if(e.target.action){
					//EXECUTE ACTION
					if(this.props.params.breadcrumb){
						let breadcrumb = this.props.params.breadcrumb;

						let results = breadcrumb.filter((bread) => {
							return bread.name === e.target.name;
						})

						if(results.length === 1){
							let bread = results[0];

							if(bread.callback) bread.callback();
						}
					}
					console.log('EXECUTE ACTION');
				}else{
					this.setState({redirect: e.target})
				}
				break;
			default:

		}
	}

	sendMessage(data){
		if(this.iframeHandler && this.iframeHandler.contentWindow){
			this.iframeHandler.contentWindow.postMessage(data, '*');
		}
		// }else{
		// 	window.removeEventListener('resize', this.handlerResize);
		// }
	}

	getDomainParams () {
		let response = {};
		if (window.location.hash && window.location.hash.includes('?')) {
			window.location.hash.split('?')[1].split`&`.forEach(item => {
				let [k,v] = item.split`=`; 
				v = v && decodeURIComponent(v); 
				
				response[k] = v;
			})
		}
		
		return response;
	}

	getCommunity(str){
		let regex = /(.*)\/([\w]+)/gm;
		let match = regex.exec(str);

		return (match) ? match[2] : null;
	}

	get visualforce(){
		return (this.props.params && this.props.params.visualforce) ? this.props.params.visualforce : 'javascriptIntegration';
	}

	get communityURI(){
		let paramsHash = this.getDomainParams();
		let paramsUrl = Lib.getUrlParams();

		if(paramsUrl.customUrl){
			return paramsUrl.customUrl;
		}

		if(paramsHash.customUrl){
			return paramsHash.customUrl;
		}

		return SalesforceAuth.communityURI;
	}

	get generateUrl(){
		let path;
		let urlReturn;

		if(this.state.redirect){
			path = `apex/${this.visualforce}?id=${this.id}&component=${this.state.redirect.name}&${this.queryString(this.state.redirect.params)}`
		}else{
			path = `apex/${this.visualforce}?id=${this.id}&component=${this.props.component}&${this.queryString(this.props.params)}`
		}

		// if(!window.Application.authenticated){
		// 	let community = this.getCommunity(this.communityURI);
		// 	urlReturn = `${this.communityURI}login?startURL=${community}/${encodeURIComponent(path)}`;
		// }else if(window.Application.authenticated){
		// }
		urlReturn = `${this.communityURI}${path}`;

		return urlReturn;
	}


	render(){
		if(!this.state.authenticated) return null;

		return <div>
			<Render condition={!this.props.params.lwc}>
				{this.renderIframe()}
			</Render>
			<Render condition={this.props.params.lwc}>
				{this.renderLwc()}
			</Render>
		</div>
	}

	handlerIframe = (e) => {
		this.iframeHandler = e;
		window.addEventListener('resize', this.handlerResize);

		//console.log('handlerIframe.setInterval.set', this.id);
		this._handlerInterval = setInterval(() => {
			//console.log('handlerIframe.setInterval.check', this.id);
			if(!this.iframeHandler || !this.iframeHandler.contentWindow){
				//console.log('handlerIframe.setInterval.remove', this.id);
				window.removeEventListener('resize', this.handlerResize);
				clearInterval(this._handlerInterval);
			}
		}, 1000);
	}

	renderIframe = () => (
		<div className="sfdc-component">
			<div className="scroll-wrapper">
				<iframe 
					id={this.id} 
					title={this.props.params.component} 
					className={this.props.params.iframeClass || ''} 
					ref={this.handlerIframe} 
					width="100%" 
					src={this.generateUrl}
				></iframe>
			</div>
		</div>
	)

	handlerResize = () => {
		clearTimeout(this._handlerUpdateTimeout);
		this._handlerUpdateTimeout = setTimeout(this.handlerUpdateCollector, 127);
	}

	handlerUpdateCollector = (e) => {
		//console.log('handlerUpdateCollector', this.id);
		this.onUpdate();
	}

	renderLwc = () => (
		<div className="sfdc-component">
			<div id={this.id} />
		</div>
	)

	componentDidMount(){		
		if(this.props.params.lwc){
			this.inizializeLightningOut();
		}
	}

}