import $ from "jquery";

export class Lib{

	static merge(){
		let result = {}, obj;
		for (let i = 0; i < arguments.length; i++) {
			obj = arguments[i];
			for (let key in obj) {
				if (Object.prototype.toString.call(obj[key]) === '[object Object]') {
					if (typeof result[key] === 'undefined') {
						result[key] = {};
					}
					result[key] = this.merge(result[key], obj[key]);
				} 
				else {
					result[key] = this.compare(result[key],obj[key]);
				}
			}
		}
		return result;
	}

	static clone(obj){
		return this.merge({},obj);
	}


	static compare(a,b){
		return b;
	}

	static downloadFile(value, nameFile){
		let url = URL.createObjectURL( new Blob( [value], {type:'text/plain'} ) );
		let anchor = document.createElement('a');
		anchor.href = url;
		anchor.target = '_blank';
		anchor.download = nameFile;
		anchor.click();	
	}

	static validation(regExObject, str){
		let regex = regExObject;
		let m;

		let values = {
			matchs: [],
			isValid: function () {
				return this.matchs.length > 0
			}
		};


		while ((m = regex.exec(str)) !== null) {
			// This is necessary to avoid infinite loops with zero-width matches
			if (m.index === regex.lastIndex) {
				regex.lastIndex++;
			}
			
			// The result can be accessed through the `m`-variable.
			m.forEach((match, groupIndex) => {
				values.matchs.push(match);
			});
		}	

		return values;
	}

	static scrollTop(element, delay){
		$("body, html").animate({scrollTop: element.offset().top}, delay);
	}

	static genGuid(prefix){
		let temp = "";
		if (prefix) temp = prefix + "-";

		function s4() {
			return Math.floor((1 + Math.random()) * 0x10000)
				.toString(16)
				.substring(1);
		}
		let gen = temp + s4() + s4() + '_' + s4() + '_' + s4() + '_' + s4() + '_' + s4() + s4() + s4();
		return gen;

	}

	static addToList(list, toAdd, criteia){
		if(list.length === 0 ) list.push(toAdd);

		let addedElement = false;
		for(let key in list){
			let value = list[key];

			if(criteia(value, toAdd)){
				value = toAdd;
				addedElement = true;
				break;
			}
		}

		if(!addedElement){
			list.push(toAdd);
		}

		return list;
	}

	static toggleToList(list, toToggle, criteia){
		if(this.existToList(list, toToggle, criteia)){
			return this.removeToList(list, toToggle, criteia);
		}

		return this.addToList(list, toToggle, criteia);
	}

	static existToList(list, toSearch, criteia){
		if(list.length === 0 ) return false;

		for(let key in list){
			let value = list[key];

			if(criteia(value, toSearch)){
				return true
			}
		}

		return false;
	}

	static removeToList(list, toRemove, criteia){
		if(list.length === 0 ) return list;

		let keyToDelete = null;

		for(let key in list){
			let value = list[key];

			if(criteia(value, toRemove)){
				keyToDelete = key;
				break;
			}
		}

		if(keyToDelete != null){
			list.splice(keyToDelete, 1);
		}
		return list;
	}

	static each(elements, interator){
		for(let key in elements){
			let element = elements[key];

			interator(element, key);
		}
	}

	static filter(elements, compare, condition){
		let listReturn = [];
		for(let key in elements){
			let element = elements[key];

			if(condition(element, compare) != null){
				listReturn.push(element);
			}
		}

		return listReturn;
	}

	static mapping(elements, manipulation){
		let listReturn = [];
		for(let key in elements){
			let element = elements[key];

			let worked = manipulation(element, key);
			if( worked != null){
				listReturn.push(worked);
			}
		}

		return listReturn;
	}

	static search(elements, compare, condition){
		for(let key in elements){
			let element = elements[key];

			if(condition(element, compare, key)){
				return element
			}

		}
	}

	static rndRangeInt(min, max){
		return Math.floor(Math.random() * (max - min + 1)) + min;
	}

	static rndRange(min, max){
		return Math.random() * (max - min) + min;
	}

	static collectionModel(model, data){
		let returnCollection = [];
		for(let key in data){
			if (isNaN(key)) continue;
			let value = data[key];

			returnCollection.push(new model(value));
		}

		return returnCollection;
	}

    static getUrlParams () {
		let response = {};
		if (window.location.search) {
			window.location.search.substr(1).split`&`.forEach(item => {
				let [k,v] = item.split`=`; 
				v = v && decodeURIComponent(v); 
				
				response[k] = v;
			})
		}
		
		return response;
	}

	static cache(uid, invoke){
		return new Promise((resolve, reject)=>{
			let dataCached = Lib.getCache('uid');

			if(dataCached == null){
				let store = (data)=>{
					localStorage.setItem(uid, JSON.stringify(data));

					resolve(data);
				}

				let fail = (err)=>{
					reject(err)
				}

				invoke(store, fail)
			}else{
				resolve(dataCached)
			}
		})
	}

	static setCache(uid, data){
		localStorage.setItem(uid, JSON.stringify(data));
	}

	static getCache(uid, defaultValue){
		let data = localStorage.getItem(uid);

		if(data == null) return defaultValue;
		
		return JSON.parse(data);
	}

	static getGps(){
		return new Promise((resolve,reject)=>{
			if (navigator.geolocation === undefined) throw new Error('Geolocation non avaiable')

			navigator.geolocation.getCurrentPosition((_position) => {
				resolve(_position.coords);
			},(error) => {
				reject(error);
			});
		})
	}
}


